/**
 * 
 *
 * 
 * Windows
 * URI(http, https, ftp ) 2(`//www.example.com/abc/` )
 *
 *  -  `\a\b\c.html`  `/a/b/c.html` 
 *  -  `/a/b////c.html`  `/a/b/c.html` 
 *  -  `C:\a\b\c.html`  `/a/b/c.html` 
 *  -  `http://a/b/c.html`  `http://a/b/c.html` URI
 *  -  `//a/b/c.html`  `//a/b/c.html` 
 *
 * @param string $path 
 * @return string 
 */
module.exports = function($path){
    $path = this.trim($path);
    // $path = $this->convert_encoding( $path );//
    $path = $path.replace( /\/|\\/g, '/' );//
    $path = $path.replace( /^[A-Za-z]\:\//g, '/' );//Windows
    var $prefix = '';
    if( $path.match( /^((?:[a-zA-Z0-9]+\:)?\/)(\/[\s\S]*)$/, $path ) ){
        $prefix = RegExp.$1;
        $path = RegExp.$2;
    }
    $path = $path.replace( /\/+/g, '/' );//1
    return $prefix+$path;
}
